/* Layout Section ------------------------------------------------------------------------ */
body {
  background: #9c9f84;
  margin: 0;
  font-family: Verdana, Georgia, serif;
  font-size: 14px;
}

#header {
  background-color: #5c755e;
  margin: 10px;
  height: 90px;
  border-radius: 5px;
}

#navbar ul {
  padding: 5px;
  list-style-type: none; /* this get rid of the bollet point of list items */
  text-align: center;
  background-color: #000000;
  margin: 10px;  
  border-radius: 5px;
}

#navbar ul li {
  display: inline;
}

#navbar ul li a {
  text-decoration: none; /* get rid of the underline of the anchor */
  font-family: Arial, Helvetica, sans-serif;
  padding: .2em 1em;
  color: #fff;
  background-color: #000000; 
}

/* when overing we reverse the colors of forward and background */
#navbar ul li a:hover {
  color: #000;
  background-color: #fff;
}

.main {
  background-color: #e5e4d7;
  margin-left: 10px;
  margin-right: 320px;
  margin-top: 10px;
  margin-bottom: 10px;

  border-radius: 5px;
  padding: 20px;
  font-size: 110%;
}

h1 {
  font-size: 120%;
  color: #ce282e;
}

h2 {
  font-size: 120%;
  color: darkblue;
}

.sideright {
  background-color: #e5e4d7;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 0;
  margin-bottom: 10px;
  border-radius: 5px;
  padding: 20px;
  font-size: 105%;
  float: right;
  width: 260px;
  clear: right;
}

.imagesideright {
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 0;
  margin-bottom: 10px;
  border-radius: 5px;
  font-size: 105%;
  float: right;
  width: 300px;
  clear: right;
  /* opacity: 1.0 => full display (regular) */
  opacity: 1.0;
  /* the why transition when it is hover */
  transition: opacity 0.25s ease-in-out;
}

/* make sure there is no space between the class name and the hover */
.imagesideright:hover {
  opacity: 0.4;
}

#footer {
  background-color: #5c755e;
  margin: 10px; 
  height: 60px;
  /* the clear: both protect the footer from the sidebars are that not in the flow to override it */
  clear: both;
  border-radius: 5px;

  /* style of the text in the footer */
  color: #efe5d0;
  text-align: center;
  padding: 15px;
  font-size: 90%;
}

#footer ul {
  padding: 5px;
  list-style-type: none; /* this get rid of the bollet point of list items */
  text-align: center;
  margin: 10px;  
  border-radius: 5px;
}

#footer ul li {
  display: inline;
}

#footer ul li a {
  text-decoration: none; /* get rid of the underline of the anchor */
  font-family: Arial, Helvetica, sans-serif;
  padding: .2em 1em;
  color: #efe5d0;
  background-color: #5c755e; 
}

/* when overing we reverse the colors of forward and background */
#footer ul li a:hover {
  color: #000;
  background-color: #fff;
}

/* ----------------------------------------------------------------------------------------- */

/* Heading Section --------------------------------------------------------------------------*/
h1 {
  font-size: 120%;
  color: #ce282e;
}

h2 {
  font-size: 120%;
  color: darkblue;
}

#header h3 {
  font-size: 250%;
  text-align: center;
  color: #e5e4d7;
  font-family: "Century Gothic", "Gill Sans", Arial, Helvetica, sans-serif;

  /* the h3 inherit the margin of 10px from the #header rule so needs to reset it*/
  margin: 0;

  /* rule for veticaly align any text */
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
/* ----------------------------------------------------------------------------------------- */